import { isVue3 } from "vue-demi";
import { viewer } from "./index";
import { OBSERVER_PLUGIN_FLAG } from "./constants/index";
const unregister = (el) => {
    const ob = el[OBSERVER_PLUGIN_FLAG];
    ob && ob.disconnect();
};
const createDirectiveHooksV2 = (options) => {
    return {
        inserted(el) {
            viewer(el, options);
        },
        unbind(el) {
            unregister(el);
        },
    };
};
const createDirectiveHooksV3 = (options) => {
    return {
        mounted(el) {
            viewer(el, options);
        },
        beforeUnmount(el) {
            unregister(el);
        },
    };
};
export const createDirective = (options) => {
    const directive = isVue3
        ? createDirectiveHooksV3(options)
        : createDirectiveHooksV2(options);
    return directive;
};
export function createVueImageViewerPlugin(options) {
    const directive = createDirective(options);
    const plugin = {
        options,
        install(app) {
            app.directive(options?.directiveName || "viewer", directive);
        },
    };
    return plugin;
}
